@font-face {
	font-family: MontserratLite;
	font-display: auto;
	src: url('/static/fonts/Montserrat-Light.otf');
}

html,
body {
	height: 100%;
}

body {
	background-color: #f5f5f5;
	color: #666;
}

/*------------ NAVBAR STYLING ----------------*/

.container {
	max-width: 65em;
}

.navbar {
	background-color: #ffffff;
}

.navbar-header {
	height: 60px;
}

.nav-logo {
	background: url('/static/images/PH/ph-logo.png');
	background-size: 100% 100%;
	height: 30px;
	width: 30px;
	position: absolute;
	top: 15px;
}

.navbar-brand {
	padding-top: 20px;
}

.nav-name {
	padding-left: 40px;
	font-size: 1.7em;
}

.navbar li a {
	padding-left: 10px;
	padding-right: 10px;
}

.navbar a {
	font-family: MontserratLite, sans-serif !important;
	color: #666;
	font-size: 1.3em;
	/* padding-top: 20px !important; */
	padding-top: 20px !important;
	-webkit-transition: color 300ms;
	-o-transition: color 300ms;
	transition: color 300ms;
}

.navbar a:hover,
.navbar a:focus {
	color: #888894;
}

.navbar li > a:hover,
.navbar li > a:focus {
	background-color: #f5f5f5;
	color: #888894;
}

.nav-container {
	border-bottom: 1px solid #e9e9e9;
}

/*-------------- PAGE WRAP -------------- */

#pageWrap {
	/* Pushes footer to bottom */
	min-height: 100%;
	margin-bottom: -220px;
}

#pageWrap:after {
	content: '';
	display: block;
	height: 200px;
}

/*------------ All Pages ------------- */

h3 {
	padding-bottom: 10px;
}

.section {
	min-height: 100%;
	/* padding-top: 5em; */

	text-align: center;
	font-family: MontserratLite;
}

