 }*/

.section-container {
	max-width: 55em;
	/* max-width: 57.5em; */
	/* width: 90%; */
	margin: 0 auto;
}

.section-container h1 {
	font-family: Montserrat;
	font-size: 3em;
}

/* .alert-section + .section {
	padding-top: 0.2em;
} */

.content {
	display: inline-block;
}

.content p {
	font-family: Roboto Slab;
	font-size: 1.1em;
}

.left {
	width: 45%;
}

label {
	text-align: left;
	width: 100%;
}

/*--------- ABOUT SECTION STYLING -----------*/

.about-img {
	margin-top: 4em;
	background: url('/static/images/PH/purdue.svg') no-repeat center;
	background-clip: padding-box;
	background-size: 130% 130%;
	height: 200px;
	width: 100%;
	border-radius: 100em;
}

.buttn {
	margin-top: 3em;
	padding: 0.7em;
	padding-left: 1em;
	padding-right: 1em;
	border-radius: 50em;

	border: 0px;
	outline: 0;
	cursor: pointer;
	font-size: 1.3em;
	color: #fff;
	background-color: #eeda69;
}

.buttn:hover {
	background-color: #e6d36a;
	-webkit-transition: background-color 300ms;
	-o-transition: background-color 300ms;
	transition: background-color 300ms;
}

/*----------- EVENT SECTION STYLING --------*/

.events {
	text-align: left;
}

.event-img {
	clear: none;
	float: right;
	margin-top: 1em;
	background: url('/static/images/PH/hacking.svg') no-repeat center;
	background-clip: padding-box;
	background-size: 130% 130%;
	height: 600px;
	max-height: 600px;
	width: 45%;
	border-radius: 100em;
}

/*----------- FAQ SECTION STYLING ---------*/

.faq {
	text-align: left;
}

.faq h1 {
	margin-left: 55%;
}

.right {
	width: 45%;
	margin-left: 10%;
}

.faq-img {
	clear: none;
	float: left;
	margin-top: 1em;
	background: url('/static/images/PH/bell_tower.svg') no-repeat center;
	background-clip: padding-box;
	background-size: 120% 120%;
	height: 600px;
	max-height: 600px;
	width: 45%;
	border-radius: 100em;
}

/*-------- SPONSOR SECTION STYLING -------*/

.sponsors {
	min-height: calc(100% - 250px) !important;
	/* padding-bottom: 4em; */
}

.sponsor-img {
	margin-top: 4em;
	background: url('/static/images/PH/city.svg') no-repeat center;
	background-clip: padding-box;
	background-size: 130% 130%;
	height: 200px;
	width: 100%;
	border-radius: 100em;
}

.sponsor-contact {
	margin-bottom: 2em;
}

/*--------- FOOTER SECTION STYLING ---------*/

.footer {
	height: 170px;
	margin-top: 30px;
	text-align: center;
	background-color: #555;
}

.footer-links {
	padding-top: 2em;
	/* padding-bottom: 1em; */
	padding-left: 0;
}

.footer-links li {
	display: inline-block;
}

.footer-links li a {
	display: inline-block;
	width: 55px;
	height: 55px;
	margin-left: 0.5em;
	padding: 6px;

	border-radius: 100%;
	border: 2px solid #aaa;
	color: #aaa;
	font-size: 2em;
	text-decoration: none;
	cursor: pointer;
	-webkit-transition: all 300ms;
	-o-transition: all 300ms;
	transition: all 300ms;
}

.footer-links li a:hover {
	color: #eee;
	border-color: #eee;
}

.footer-links .facebook {
	margin-left: 0;
}

.footer-links .facebook a {
	margin-left: 0;
}

.footer h4 {
	color: #aaa;
	font-family: MontserratLite;
}

.fa-heart {
	color: #f77380;
}

.fa-coffee {
	color: #eee;
}

.footer h4 a {
	color: #ccc;
}

.footer h4 a:hover {
	color: #eee;
	text-decoration: none;
}

/*------------- MEDIA QUERIES ------------*/

@media (max-width: 1200px) {
	.section {
		/* padding-top: 4em; */
	}
	.section-container h1 {
		font-size: 3em;
	}
	.section-container h3 {
		font-size: 1.5em;
	}
}

@media (max-width: 992px) {
	.event-img,
	.faq-img {
		max-height: 550px;
	}
}

@media (max-width: 768px) {
	.event-img,
	.faq-img {
		display: none;
	}
	.left,
	.right {
		width: 100%;
	}
	.right,
	.faq h1 {
		margin-left: 0;
	}
}
@media (max-width: 600px) {
	.about-img,
	.sponsor-img {
		height: 140px;
	}
}

@media (max-width: 480px) {
	.section-container h1 {
		font-size: 2em;
	}
	.section-container h3 {
		font-size: 1.2em;
	}
	.buttn {
		font-size: 1em;
	}
	.about-img,
	.sponsor-img {
		height: 120px;
	}
	.footer-links {
		padding-top: 1em;
		/* padding-bottom: 0.5em; */
	}
	.footer-links li a {
		width: 40px;
		height: 40px;
		font-size: 1em;
	}
	.footer h4 {
		font-size: 0.7em;
	}
	.footer {
		height: 100px;
	}
}

/*------------- Pulse Animation ------------*/
@-webkit-keyframes pulse {
	0% {
		-webkit-transform: scale(1.1);
		transform: scale(1.1);
	}

	50% {
		-webkit-transform: scale(0.8);
		transform: scale(0.8);
	}

	100% {
		-webkit-transform: scale(1.1);
		transform: scale(1.1);
	}
}

@keyframes pulse {
	0% {
		-webkit-transform: scale(1.1);
		-ms-transform: scale(1.1);
		transform: scale(1.1);
	}

	50% {
		-webkit-transform: scale(0.8);
		-ms-transform: scale(0.8);
		transform: scale(0.8);
	}

	100% {
		-webkit-transform: scale(1.1);
		-ms-transform: scale(1.1);
		transform: scale(1.1);
	}
}

.faa-pulse.animated,
.faa-pulse.animated-hover:hover,
.faa-parent.animated-hover:hover > .faa-pulse {
	-webkit-animation: pulse 2s linear infinite;
	animation: pulse 2s linear infinite;
}

/* Tables
 –––––––––––––––––––––––––––––––––––––––––––––––––– */
th,
td {
	padding: 12px 15px;
	text-align: left;
	border-bottom: 1px solid #e1e1e1;
}
th:first-child,
td:first-child {
	padding-left: 0;
}
th:last-child,
td:last-child {
	padding-right: 0;
}

.table-clickable tbody tr:hover {
	cursor: pointer;
}

/* Utilities
 –––––––––––––––––––––––––––––––––––––––––––––––––– */
.u-full-width {
	width: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.u-max-full-width {
	max-width: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.u-pull-right {
	float: right;
}
.u-pull-left {
	float: left;
}

/* Misc
 –––––––––––––––––––––––––––––––––––––––––––––––––– */
hr {
	margin-top: 3rem;
	margin-bottom: 3.5rem;
	border-width: 0;
	border-top: 1px solid #e1e1e1;
}

.member-icon {
	max-width: 100px;
	max-height: 100px;
}

.marginR {
	margin-right: 8px;
}

.obscure {
	color: transparent;
	text-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.obscure:hover {
	color: #000000;
	text-shadow: none;
}

/* ------------------------ Graphs ------------------------- */

.graph {
	display: block;
	width: 100%;
	height: 250px;
}

/* ------------------------ Profile ------------------------- */

#profile_image {
	float: left;
	margin-left: 50px;
	height: 200px;
	border-radius: 100px;
	-webkit-border-radius: 100px;
	-moz-border-radius: 100px;
}

#profile_intro_text {
	text-align: center;
	padding: 10px;
}

#profile_name {
	color: #000000;
	font-size: 32px;
}

#profile_email {
	display: block;
	font-size: 14px;
	margin-top: -4px;
	color: #444444;
	text-decoration: none;
}

#profile_major {
	font-size: 1.1em;
	margin-top: 8px;
	color: #444444;
}

#profile_badges {
	margin-top: 20px;
}

.profile_badge {
	display: inline-block;
	font-size: 20px;
	padding: 0px 10px;
}

.profile_badge_title {
	color: #eeda69;
	margin-bottom: 6px;
}

/* Clearing
 –––––––––––––––––––––––––––––––––––––––––––––––––– */

/* Self Clearing Goodness */
.container:after,
.row:after,
.u-cf {
	content: '';
	display: table;
	clear: both;
}
